:root {
  font-family: "helvetica neue", helvetica, sans-serif;
  --default-font: "helvetica neue", helvetica, sans-serif;
}

.block-editor-rich-text__editable a {
  color: #000000;
}

body .is-root-container {
  padding: 0px;
}

body.post-type-post .is-root-container {
  max-width: 960px;
  padding-left: 30px;
  padding-right: 30px;
  margin-left: auto;
  margin-right: auto;
}

html :where(.editor-styles-wrapper) {
  font-family: "helvetica neue", helvetica, sans-serif;
}

.editor-styles-wrapper .block-list-appender {
  margin-top: 0px;
  margin-bottom: 0px;
}

/****************************************************************************************/
/* Typography + font changes for editor only (frontend color is on body) */
/****************************************************************************************/
/* post title */
:root,
body .is-root-container {
  font-family: "helvetica neue", helvetica, sans-serif;
}

.editor-styles-wrapper .editor-post-title {
  margin: 0px auto;
}

.editor-post-title__block .editor-post-title__input {
  padding: 16px;
  font-weight: 700;
}

.block-editor-format-toolbar__link-container-value,
[data-rich-text-placeholder],
.editor-styles-wrapper .block-editor-default-block-appender textarea.block-editor-default-block-appender__content {
  word-wrap: break-word;
  font-size: 16px;
}

html :where(.editor-styles-wrapper) p {
  line-height: 1.6;
}

.editor-styles-wrapper .wp-block[data-type="core/heading"] h1,
.editor-styles-wrapper .wp-block[data-type="core/heading"] h2,
.editor-styles-wrapper .wp-block[data-type="core/heading"] h3,
.editor-styles-wrapper .wp-block[data-type="core/heading"] h4,
.editor-styles-wrapper .wp-block[data-type="core/heading"] h5,
.editor-styles-wrapper .wp-block[data-type="core/heading"] h6 {
  margin-top: 0px;
  margin-bottom: 5px;
}

.editor-styles-wrapper .editor-post-title [data-rich-text-placeholder] {
  font-size: inherit;
}

p.wp-block-subhead,
.subhead-h,
.c9-sh,
.editor-styles-wrapper .c9-cta .c9-sh p {
  font-weight: 300;
}

.editor-styles-wrapper .is-root-container > p,
.editor-styles-wrapper .is-root-container > .block-list-appender > .has-visible-prompt > p,
.wp-block[data-type="core/paragraph"] p,
.editor-styles-wrapper .block-editor-default-block-appender textarea.block-editor-default-block-appender__content {
  line-height: 1.6;
  margin-bottom: 20px;
  margin-top: 0px;
  margin-left: auto;
  margin-right: auto;
}

.editor-styles-wrapper a:not(.wp-block-button__link) {
  text-decoration: underline;
}

body .editor-styles-wrapper,
.editor-styles-wrapper .wp-block-table,
.editor-styles-wrapper .wp-block-file,
.editor-post-title__block .editor-post-title__input,
.editor-styles-wrapper .wp-block-quote p,
.editor-styles-wrapper h1,
.editor-styles-wrapper h2,
.editor-styles-wrapper h3,
.editor-styles-wrapper h4,
.editor-styles-wrapper h5,
.editor-styles-wrapper h6,
.wp-block-button.is-style-outline .wp-block-button__link {
  color: #4a4a4a;
}

.wp-block-button.is-style-squared .wp-block-button__link,
.wp-block-button .wp-block-button__link {
  color: #ffffff;
}

.editor-styles-wrapper .mce-content-body p:last-child,
.editor-styles-wrapper blockquote p:last-child,
.editor-styles-wrapper .wp-block-quote p:last-child,
.wp-block-pullquote.is-style-solid-color blockquote p:last-child,
.editor-styles-wrapper .mce-content-body p:only-of-type,
.editor-styles-wrapper blockquote p:only-of-type,
.editor-styles-wrapper .wp-block-quote p:only-of-type,
.wp-block-pullquote.is-style-solid-color blockquote p:only-of-type {
  margin-bottom: 30px;
}

.editor-styles-wrapper .wp-block-pullquote blockquote:before {
  top: -30px;
}

/****************************************************************************************/
/* Backend editor widths setup and default max widths for Blocks not in C9 Grid Containers
/****************************************************************************************/
.block-editor-editor-skeleton__content .editor-styles-wrapper {
  margin: 0px auto;
  border-left: 1px solid #d6d6d6;
  border-right: 1px solid #d6d6d6;
}

body.post-type-page .is-sidebar-opened .block-editor-editor-skeleton__content .editor-styles-wrapper {
  max-width: 1140px;
}

@media only screen and (min-width: 768px) {
  .post-type-post .editor-styles-wrapper .wp-block {
    max-width: 960px;
  }

  .editor-styles-wrapper .is-root-container .wp-block[data-align=full] {
    max-width: 100%;
    margin-left: 0px;
    margin-right: 0px;
  }
}
@media only screen and (min-width: 992px) {
  .is-sidebar-opened .editor-styles-wrapper .wp-block .is-sidebar-opened .editor-styles-wrapper .wp-block {
    max-width: 100vw;
    max-width: 100vw;
  }

  .post-type-page .is-sidebar-opened .edit-post-visual-editor__post-title-wrapper {
    max-width: 1140px;
    margin-left: auto;
    margin-right: auto;
  }

  .post-type-post .is-sidebar-opened .edit-post-visual-editor__post-title-wrapper h1,
.post-type-page .is-sidebar-opened .edit-post-visual-editor__post-title-wrapper h1 {
    padding-left: 0px;
    padding-right: 0px;
  }
}
@media only screen and (min-width: 1200px) {
  .post-type-post .is-root-container > .wp-block {
    max-width: 960px;
  }

  .editor-styles-wrapper .is-root-container > .wp-block {
    max-width: 1140px;
    margin-right: auto;
    margin-left: auto;
  }

  .post-type-page .editor-styles-wrapper .is-root-container > .wp-block {
    max-width: 1140px;
  }

  .editor-styles-wrapper .block-list-appender.wp-block {
    margin-left: 0px;
    margin-right: 0px;
  }

  .editor-styles-wrapper .is-root-container > .block-list-appender.wp-block {
    margin-left: auto;
    margin-right: auto;
  }

  .editor-styles-wrapper .c9-grid .wp-block[data-type="c9-blocks/column-container"] .wp-block[data-type="core/button"],
.is-sidebar-opened .editor-styles-wrapper .c9-grid [data-type="c9-blocks/column-container"].container .wp-block[data-type="core/button"] {
    margin-left: 0px;
    margin-right: 0px;
  }

  .editor-styles-wrapper .c9-grid .wp-block[data-type="c9-blocks/column-container"] .wp-block-buttons > .wp-block[data-type="core/button"]:only-of-type {
    margin-right: 0;
  }

  .editor-styles-wrapper .c9-grid .wp-block[data-type="c9-blocks/column-container"] .wp-block-buttons .wp-block[data-type="core/button"]:last-child {
    margin-right: 0px;
  }

  body.post-type-post .editor-styles-wrapper .wp-block {
    max-width: 900px;
  }

  .post-type-post .editor-styles-wrapper .is-root-container > .wp-block[data-align=wide] {
    width: 1080px;
    margin-left: auto;
    margin-right: auto;
    max-width: 100vw;
  }

  .post-type-page .editor-styles-wrapper .is-root-container > .wp-block[data-align=wide] {
    max-width: 1368px;
  }

  .post-type-page .is-sidebar-opened .editor-styles-wrapper .is-root-container > .wp-block[data-align=full],
.post-type-post .is-sidebar-opened .editor-styles-wrapper .is-root-container > .wp-block[data-align=full] {
    max-width: calc(100vw - 280px);
    width: 100%;
    margin-left: 0;
    margin-right: 0;
  }

  .edit-post-layout:not(.is-sidebar-opened) .editor-styles-wrapper .block-editor-block-list__layout {
    padding-left: 0px;
    padding-right: 0px;
  }
}
@media only screen and (min-width: 1500px) {
  /* full screen width on large screens */
  body.post-type-page .block-editor-editor-skeleton__content .editor-styles-wrapper,
body.post-type-page .is-sidebar-opened .block-editor-editor-skeleton__content .editor-styles-wrapper {
    max-width: 94%;
  }
}
/****************************************************************************************/
/***************************** core block edits for backend only
/****************************************************************************************/
/* embeddable tweets */
.wp-block[data-type="core-embed/twitter"] .wp-block-embed iframe {
  margin-left: auto;
  margin-right: auto;
  width: auto;
  min-width: 550px;
}

.wp-block[data-type="core-embed/twitter"] .wp-block-embed .wp-block-embed__wrapper {
  text-align: center;
}

/*WordPress core separator */
.wp-block[data-type="core/separator"] .block-editor-block-list__block-edit {
  max-width: 1110px;
  padding-top: 10px;
  padding-bottom: 10px;
  margin-top: 0px;
  margin-bottom: 0px;
}

.wp-block[data-type="core/separator"] .block-editor-block-list__block-edit .wp-block-separator {
  margin-top: 0px !important;
  margin-bottom: 0px !important;
}

/* WordPress core tables */
.editor-styles-wrapper .wp-block-table {
  margin: 0px auto 25px auto;
}

/* WordPress core square buttons */
/* center alignment for buttons block */
.wp-block[data-align=center] .wp-block-buttons .block-editor-block-list__layout {
  text-align: center;
}

.wp-block-buttons [data-rich-text-placeholder] {
  font-size: 14px;
}

/*WordPress 5.7 button alignments*/
.editor-styles-wrapper .wp-block-buttons .wp-block-button {
  margin-right: 0.5em;
  margin-bottom: 0.5em;
  display: inline-block;
}

.editor-rich-text__editable.wp-block-button__link {
  width: 100%;
}

.editor-styles-wrapper .wp-block-buttons > .wp-block-button:last-child {
  margin-right: 0;
}

.wp-block[data-type="core/paragraph"] ~ .wp-block[data-type="core/button"]:not(.alignleft) {
  margin-top: 20px;
}

.wp-block[data-type="core/button"][data-align=left],
.wp-block[data-type="core/button"][data-align=right] {
  display: block;
  width: auto;
}

.wp-block[data-type="core/button"][data-align=left] {
  float: left;
}

.wp-block[data-type="core/button"][data-align=right] {
  float: right;
}

/* WordPress 6.1 button updates */
.editor-styles-wrapper .c9-grid .wp-block[data-type="c9-blocks/column-container"] .wp-block[data-type="core/button"].aligncenter,
.is-sidebar-opened .editor-styles-wrapper .c9-grid [data-type="c9-blocks/column-container"].container .wp-block[data-type="core/button"].aligncenter {
  margin-left: auto;
  margin-right: auto;
}

@media only screen and (min-width: 1200px) {
  .editor-styles-wrapper .wp-block[data-align=center] .wp-block-buttons {
    text-align: center;
  }

  .editor-styles-wrapper .wp-block[data-align=center] .wp-block-buttons > .wp-block-button {
    margin-left: 0px;
    margin-right: 5px;
  }

  .editor-styles-wrapper .wp-block[data-align=center] .wp-block-buttons > .wp-block-button:last-child {
    margin-right: 0px;
  }
}
/* WordPress core list items */
.editor-styles-wrapper ul li div,
.editor-styles-wrapper ul:not([class]) li div,
.editor-styles-wrapper ul[class=""] li div,
.editor-styles-wrapper ol li div,
.editor-styles-wrapper ol:not([class]) li div,
.editor-styles-wrapper ol[class=""] li div {
  display: inline;
}

html :where(.editor-styles-wrapper) ul,
html :where(.editor-styles-wrapper) ol {
  -webkit-padding-start: 0;
  padding-inline-start: 0;
  padding: 0px 0px 0px 30px;
}

/* WordPress Core search widget block */
.editor-styles-wrapper .wp-block-search .wp-block-search__label {
  margin-bottom: 8px;
  font-size: 18px;
}

.editor-styles-wrapper .wp-block-search .wp-block-search__input {
  min-height: 38px;
}

.editor-styles-wrapper .wp-block-search .wp-block-search__button,
.editor-styles-wrapper .wp-block-search__button {
  text-transform: uppercase;
  font-weight: 500;
  font-size: 14px;
  background: #000000;
  box-shadow: none;
  border-radius: 0px;
  border: none;
  padding: 12px 35px;
  color: #ffffff;
}

/* WordPress core spacers */
.editor-styles-wrapper [data-type="core/spacer"] {
  margin-top: 20px;
  margin-bottom: 20px;
}

/* WordPress image blocks */
.editor-styles-wrapper .wp-block-image.aligncenter,
.wp-block-image.aligncenter > figcaption,
.wp-block-image.alignleft > figcaption,
.wp-block-image.alignright > figcaption,
.wp-block-image.is-resized > figcaption {
  display: block;
}

[data-align=center] .wp-block-image .components-resizable-box__container,
[data-align=center] .wp-block-image img,
.wp-block[data-align=center] > .wp-block-image img,
.aligncenter.wp-block-image img,
.aligncenter.wp-block-image > div {
  margin-left: auto;
  margin-right: auto;
}

[data-type="core/image"] img {
  margin-left: 0;
  margin-right: 0;
}

.wp-block[data-align=left] [data-type="core/image"],
.wp-block[data-align=left] > .wp-block-image,
.alignleft.wp-block-image > img {
  margin-left: 0px;
  margin-right: 30px;
}

.wp-block[data-align=left] > .wp-block-image,
.wp-block[data-align=left] > .wp-block-image > .components-resizable-box__container,
.alignleft.wp-block-image > img,
.alignleft.wp-block-image > div {
  float: left;
}

.wp-block[data-align=right] [data-type="core/image"],
.wp-block[data-align=right] > .wp-block-image,
.alignright.wp-block-image img {
  margin-left: 30px;
  margin-right: 0px;
}

.wp-block[data-align=right] > .wp-block-image,
.wp-block[data-align=right] > .wp-block-image > .components-resizable-box__container,
.alignright.wp-block-image img,
.alignright.wp-block-image > div {
  float: right;
}

/* WordPress cover block and core image block on post edit (not page edit) */
/* WordPress core quote blocks on Posts and elsewhere */
.editor-styles-wrapper .wp-block-quote,
.editor-styles-wrapper .wp-block-quote:not(.is-large):not(.is-style-large) {
  margin: 20px auto 30px auto;
  margin-left: auto;
}

/* pullquote blocks */
@media only screen and (min-width: 992px) {
  .editor-styles-wrapper .wp-block[data-type="core/pullquote"] .wp-block-pullquote {
    max-width: 960px;
    margin-left: auto;
    margin-right: auto;
  }
}
/* code blocks */
code,
kbd,
pre,
html :where(.editor-styles-wrapper) code,
html :where(.editor-styles-wrapper) kbd {
  background-color: transparent;
}

.wp-block-code > code {
  border: none;
}

/* social link block */
.editor-styles-wrapper .wp-block-social-links li:last-child {
  margin-right: 0px;
}

.editor-styles-wrapper .wp-block-social-links.aligncenter {
  justify-content: center;
}

.editor-styles-wrapper .wp-block-social-links.alignleft {
  justify-content: flex-start;
}

.editor-styles-wrapper .wp-block-social-links.alignright {
  justify-content: flex-end;
}

/* make mobile editing on gutenberg possible */
@media only screen and (max-width: 414px) {
  body.post-type-post .block-editor-editor-skeleton__content .editor-styles-wrapper,
body.post-type-page .block-editor-editor-skeleton__content .editor-styles-wrapper {
    max-width: 100vw;
    margin: 0px auto;
    width: 100%;
  }

  .block-editor-block-list__layout {
    max-width: 100%;
  }

  .wp-block {
    max-width: 100%;
  }

  .block-editor-block-list__layout .block-editor-block-list__block[data-align=wide],
.block-editor-block-list__layout .block-editor-block-list__block[data-align=full] {
    width: 100vw;
  }
}
/* Gutenberg blocks alignwide and alignfull */
@media only screen and (min-width: 768px) {
  .edit-post-layout:not(.is-sidebar-opened) .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=full] {
    margin-left: calc((-100vw + 100% + 116px) / 2);
    margin-right: calc((-100vw + 100% + 116px) / 2);
    width: calc(100vw - 116px);
    max-width: calc(100vw - 116px);
  }

  .is-sidebar-opened .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=full] {
    width: calc(100vw - 280px);
    max-width: calc(100vw - 280px);
    margin-left: calc((-100vw + 100% + 280px) / 2);
    margin-right: calc((-100vw + 100% + 280px) / 2);
  }

  .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=wide] {
    margin-left: -10%;
    margin-right: -10%;
    width: 120%;
    max-width: 100vw;
  }

  .is-sidebar-opened .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=wide] {
    width: 100%;
    margin-left: 0;
    margin-right: 0;
  }
}
@media only screen and (min-width: 992px) {
  .is-sidebar-opened .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=wide] {
    width: 100%;
    margin-left: 0;
    margin-right: 0;
  }
}
@media only screen and (min-width: 1200px) {
  .is-sidebar-opened .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=wide] {
    width: 100%;
  }

  .is-sidebar-opened .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=full] {
    max-width: calc(100vw - 280px - 72px);
    margin-left: -58px;
    margin-right: -58px;
  }

  /* WordPress 6.1 backend widths for alignfull/alignwide and c9 grid inside of posts */
  .is-sidebar-opened .editor-styles-wrapper .edit-post-visual-editor__post-title-wrapper > [data-type="c9-blocks/grid"]:where(:not(.alignleft):not(.alignright):not(.alignfull)),
.is-sidebar-opened .editor-styles-wrapper .block-editor-block-list__layout.is-root-container > [data-type="c9-blocks/grid"]:where(:not(.alignleft):not(.alignright):not(.alignfull)) {
    margin-left: calc((-100vw + 280px + 100%) / 2) !important;
    margin-right: calc((-100vw + 280px + 100%) / 2) !important;
  }
}
@media only screen and (min-width: 1500px) {
  .edit-post-layout:not(.is-sidebar-opened) .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=wide] {
    width: 120%;
    max-width: 1368px;
    margin-left: auto;
    margin-right: auto;
  }

  .is-sidebar-opened .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=wide] {
    width: 100%;
    margin-left: 0px;
    margin-right: 0px;
  }

  .edit-post-layout:not(.is-sidebar-opened) .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=full] {
    width: calc(100vw - 116px);
    margin-left: -58px;
    margin-right: -58px;
  }

  .is-sidebar-opened .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=full] {
    max-width: calc(100vw - 280px);
    width: calc(100% + 116px);
  }
}
